const pageBody = document.body;
// –î–ª—è —É–¥–æ–±—Å—Ç–≤–∞ –ø–æ–∏—Å–∫–∞ –Ω–∞—Ö–æ–¥–∏–º —Å–µ–∫—Ü–∏—é header
const header = pageBody.querySelector('#header');
// –õ–æ–≥–æ—Ç–∏–ø —Å–∞–π—Ç–∞ –≤ –ø–ª–∞–Ω—à–µ—Ç–Ω–æ–π/–º–æ–±–∏–ª—å–Ω–æ–π –≤–µ—Ä—Å–∏–∏
const headerLogo = header.querySelector('#header__logo');
// –ö–Ω–æ–ø–∫–∞ –¥–ª—è –æ—Ç–∫—Ä—ã—Ç–∏—è/–∑–∞–∫—Ä—ã—Ç–∏—è –º–µ–Ω—é
const burger = header.querySelector('#burger');
// –≠–ª–µ–º–µ–Ω—Ç - '–ë—É—Ä–≥–µ—Ä/–ö—Ä–µ—Å—Ç–∏–∫'
const burgerMenu = burger.querySelector('#burger__menu');
// –ù–∞–≤–∏–≥–∞—Ü–∏—è –ø–æ —Å–∞–π—Ç—É
const headerNav = header.querySelector('#header__nav');
// –ù–∞–≤–∏–≥–∞—Ü–∏—è –ø–æ —Å–∞–π—Ç—É
const introBlock = pageBody.querySelector('#intro__block');

if (window.matchMedia('(max-width: 767px)').matches) {

  // –ü—Ä–æ–≤–µ—Ä–∫–∞ —Ä–∞–±–æ—Ç–æ—Å–ø–æ—Å–æ–±–Ω–æ—Å—Ç–∏ —ç–ª–µ–º–µ–Ω—Ç–æ–≤ –ø—Ä–∏ –æ—Ç–∫–ª—é—á–µ–Ω–Ω–æ–º JavaScript
  const checkWorkJs = () => {
    if (header.classList.contains('no-js')) {
      header.classList.remove('no-js');
    }
  };

  checkWorkJs();

  /* –ó–∞–∫—Ä—ã—Ç–∏–µ –º–µ–Ω—é –ø—Ä–∏ –∫–ª–∏–∫–µ –≤–Ω–µ –æ–±–ª–∞—Å—Ç–∏ –º–µ–Ω—é */
  const clickOverlay = (evt) => {
    const elements–°lickArea = !evt.composedPath().includes(headerNav);
    if (elements–°lickArea) {
      // —Å–∫—Ä—ã–≤–∞–µ–º —ç–ª–µ–º–µ–Ω—Ç —Ç.–∫. –∫–ª–∏–∫ –±—ã–ª –∑–∞ –µ–≥–æ –ø—Ä–µ–¥–µ–ª–∞–º–∏
      pageBody.classList.remove('scroll-lock');
      headerLogo.classList.remove('is-open');
      headerNav.classList.remove('is-open');
      burgerMenu.classList.remove('is-open');
      burger.classList.remove('is-open');
      introBlock.classList.remove('text-opacity');
      pageBody.classList.remove('shadow');
      document.removeEventListener('click', clickOverlay);
    }
  };

  /* –û—Ç–∫—Ä—ã—Ç–∏–µ –º–µ–Ω—é –ø—Ä–∏ –∫–ª–∏–∫–µ –Ω–∞ –±—É—Ä–≥–µ—Ä */
  burger.addEventListener('click', (evt) => {
    evt.preventDefault();
    evt.stopPropagation();
    pageBody.classList.toggle('scroll-lock');
    headerLogo.classList.toggle('is-open');
    headerNav.classList.toggle('is-open');
    burgerMenu.classList.toggle('is-open');
    burger.classList.toggle('is-open');
    introBlock.classList.toggle('text-opacity');
    pageBody.classList.toggle('shadow');
    document.addEventListener('click', clickOverlay);
  });

  /* –ü—Ä–∏ –Ω–∞–∂–∞—Ç–∏–∏ –Ω–∞ —Å—Å—ã–ª–∫—É –Ω–∞–≤–∏–≥–∞—Ü–∏–∏ —Å–∞–π—Ç–∞ - –∑–∞–∫—Ä—ã–≤–∞–µ–º –º–æ–±–∏–ª—å–Ω–æ–µ –º–µ–Ω—é –∏ —É–¥–∞–ª—è–µ–º –ø—Ä–æ—Å—Ç–∞–≤–ª–µ–Ω–Ω—ã–µ –∫–ª–∞—Å—Å—ã, —á—Ç–æ–±—ã –º–µ–Ω—é –Ω–∏ –º–µ—à–∞–ª–æ –ø—Ä–æ—Å–º–æ—Ç—Ä—É —Å—Ç—Ä–∞–Ω–∏—Ü—ã */
  headerNav.addEventListener('click', (evt) => {
    if (evt.target.matches('a')) {
      pageBody.classList.remove('scroll-lock');
      headerLogo.classList.remove('is-open');
      headerNav.classList.remove('is-open');
      burgerMenu.classList.remove('is-open');
      burger.classList.remove('is-open');
      introBlock.classList.toggle('text-opacity');
      pageBody.classList.toggle('shadow');
      document.removeEventListener('click', clickOverlay);
    }
  });
}
