// –°—Å—ã–ª–∫–∞ –Ω–∞ –≤–∏–¥–µ–æ, –æ—Ç–∫—É–¥–∞ –≤–∑—è–ª –∫–æ–¥ - https://youtu.be/Lxj_v5z0xRE
// –°—Å—ã–ª–∫–∞ –Ω–∞ –≥–∏—Ç—Ö–∞–± https://github.com/alexey-goloburdin/phoneinput

// –ù–∞—Ö–æ–¥–∏–º –≤—Å–µ –ø–æ–ª—è –≤–≤–æ–¥–∞ –Ω–æ–º–µ—Ä–∞ —Ç–µ–ª–µ—Ñ–æ–Ω–∞
const phoneInputs = Array.from(document.querySelectorAll('input[data-tel-input]'));

/* /\D/g - –≥–ª–æ–±–∞–ª—å–Ω–æ–µ —Ä–µ–≥—É–ª—è—Ä–Ω–æ–µ –≤—ã—Ä–∞–∂–µ–Ω–∏–µ. –ò—â–µ—Ç –≤—Å–µ —Å–∏–º–≤–æ–ª—ã, –∫–æ—Ç–æ—Ä—ã–µ –Ω–µ —è–≤–ª—è—é—Ç—Å—è —á–∏—Å–ª–∞–º–∏ –∏ —É–¥–∞–ª—è–µ—Ç –∏—Ö */
const regExp = /\D/g;

/* –§—É–Ω–∫—Ü–∏—è –ø—Ä–æ–≤–µ—Ä—è–µ—Ç, —á—Ç–æ –Ω–∞–∂–∞–ª–∏ –∫–Ω–æ–ø–∫—É Backspace */
const isBackspaceKey = (evt) => evt.key === 'Backspace';

/**
 * @description –§—É–Ω–∫—Ü–∏—è –æ–±—Ä–µ–∑–∞–µ—Ç –≤—Å–µ —Å–∏–º–≤–æ–ª—ã –∫—Ä–æ–º–µ —á–∏—Å–µ–ª
 * @param {text} input - —Å–∏–º–≤–æ–ª—ã –≤–≤–æ–¥–∏–º—ã–µ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª–µ–º –≤ –ø–æ–ª–µ –≤–≤–æ–¥–∞
 * @return {number}
 */
const getInputNumbersValue = function (input) {
  return input.value.replace(regExp, '');
};

// –§—É–Ω–∫—Ü–∏—è –∑–∞–ø—Ä–µ—Ç–∞ –≤—Å—Ç–∞–≤–∫–∏ –Ω–µ —á–∏—Å–ª–æ–≤—ã—Ö –∑–Ω–∞—á–µ–Ω–∏–π –≤ –ø–æ–ª–µ –≤–≤–æ–¥–∞
const onPhonePaste = function (evt) {
  const input = evt.target;
  const inputNumbersValue = getInputNumbersValue(input);
  const pasted = evt.clipboardData || window.Clipboard;
  if (pasted) {
    const pastedText = pasted.getData('Text');
    if (regExp.test(pastedText)) {
      input.value = inputNumbersValue;
      return;
    }
  }
};

// –§—É–Ω–∫—Ü–∏—è —Å–æ–∑–¥–∞–Ω—è –º–∞—Å–∫–∏ –≤–≤–æ–¥–∞ –Ω–æ–º–µ—Ä–∞ —Ç–µ–ª–µ—Ñ–æ–Ω–∞
const onPhoneInput = (evt) => {
  const input = evt.target;
  // –ü–æ–ª–æ–∂–µ–Ω–∏–µ –∫—É—Ä—Å–æ—Ä–∞
  const selectionStart = input.selectionStart;
  let inputNumbersValue = getInputNumbersValue(input);
  let formattedInputValue = '';

  // –ï—Å–ª–∏ –ø–æ—Å–ª–µ –æ–±—Ä–µ–∑–∫–∏ —Å–∏–º–≤–æ–ª–æ–≤ —Ü–∏—Ñ—Ä –Ω–µ –æ—Å—Ç–∞–ª–æ—Å—å - –æ—Å—Ç–∞–≤–ª—è–µ–º –ø—É—Å—Ç—É—é —Å—Ç—Ä–æ–∫—É
  if (!inputNumbersValue) {
    input.value = '';
    return;
  }
  /* –ü—Ä–æ–≤–µ—Ä—è–µ–º –ø–æ–ª–æ–∂–µ–Ω–∏–µ –∫—É—Ä—Å–æ—Ä–∞. –ï—Å–ª–∏ –≤ —Å–µ—Ä–µ–¥–∏–Ω–µ —Å—Ç—Ä–æ–∫–∏ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å —É–¥–∞–ª—è–µ—Ç –∏  —Ö–æ—á–µ—Ç –ø–æ–º–µ–Ω—è—Ç—å –∑–Ω–∞—á–µ–Ω–∏–µ, —Ç–æ –¥–∞–µ–º –≤–æ–∑–º–æ–∂–Ω–æ—Å—Ç—å —Ä–µ–¥–∞–∫—Ç–∏—Ä–æ–≤–∞–Ω–∏—è –±–µ–∑ –ø–µ—Ä–µ–∫–∏–¥—ã–≤–∞–Ω–∏—è –∫—É—Ä—Å–æ—Ä–∞ –≤ –∫–æ–Ω–µ—Ü —Å—Ç—Ä–æ–∫–∏ */
  if (input.value.length !== selectionStart) {
    if (evt.data && regExp.test(evt.data)) {
      input.value = inputNumbersValue;
    }
    return;
  }

  if (['7', '8', '9'].indexOf(inputNumbersValue[0]) > -1) {
    // –†–æ—Å—Å–∏–π—Å–∫–∏–π –Ω–æ–º–µ—Ä —Ç–µ–ª–µ—Ñ–æ–Ω–∞
    if (inputNumbersValue[0] === '9') {
      inputNumbersValue = '7' + inputNumbersValue;
    }
    const firstSymbols = (inputNumbersValue[0] === '8') ? '8' : '+7';
    formattedInputValue = input.value = firstSymbols + ' ';
    if (inputNumbersValue.length > 1) {
      formattedInputValue += '(' + inputNumbersValue.substring(1, 4);
    }
    if (inputNumbersValue.length >= 5) {
      formattedInputValue += ') ' + inputNumbersValue.substring(4, 7);
    }
    if (inputNumbersValue.length >= 8) {
      formattedInputValue += '-' + inputNumbersValue.substring(7, 9);
    }
    if (inputNumbersValue.length >= 10) {
      formattedInputValue += '-' + inputNumbersValue.substring(9, 11);
    }
  } else {
    // –ò–Ω–æ—Å—Ç—Ä–∞–Ω–Ω—ã–π –Ω–æ–º–µ—Ä —Ç–µ–ª–µ—Ñ–æ–Ω–∞
    formattedInputValue = '+' + inputNumbersValue.substring(0, 16);
  }
  input.value = formattedInputValue;
};

// –§—É–Ω–∫—Ü–∏—è –∏—Å–ø—Ä–∞–≤–ª—è–µ—Ç –±–∞–≥ –ø—Ä–∏ —É–¥–∞–ª–µ–Ω–∏–∏ —Å–∏–º–≤–æ–ª–æ–≤
const onPhoneKeyDown = function (evt) {
  const inputValue = evt.target.value.replace(regExp, '');
  if (isBackspaceKey(evt) && inputValue.length === 1) {
    evt.target.value = '';
  }
};

/* –ü—Ä–æ–≤–µ—Ä—è–µ–º –µ—Å—Ç—å –ª–∏ –Ω–∞ —Å—Ç—Ä–∞–Ω–∏—Ü–µ —ç–ª–µ–º–µ–Ω—Ç—ã  */
if (phoneInputs) {
  // –ü–µ—Ä–µ–±–∏—Ä–∞–µ–º –≤—Å–µ –ø–æ–ª—è –≤–≤–æ–¥–∞ —Ç–µ–ª–µ—Ñ–æ–Ω–∞ –∏ –¥–æ–±–∞–≤–ª—è–µ–º –æ–±—Ä–∞–±–æ—Ç—á–∏–∫ —Å–æ–±—ã—Ç–∏—è
  phoneInputs.forEach((input) => {
    input.addEventListener('input', onPhoneInput);
    input.addEventListener('keydown', onPhoneKeyDown, false);
    input.addEventListener('paste', onPhonePaste, false);
  });
}
