// –°–µ–∫—Ü–∏—è —Å –Ω—É–∂–Ω—ã–º–∏ –Ω–∞–º —ç–ª–µ–º–µ–Ω—Ç–∞–º–∏ –¥–ª—è —É–¥–æ–±—Å—Ç–≤–∞ –ø–æ–∏—Å–∫–∞
const directionsBlock = document.querySelector('#directions');
// –°–ø–∏—Å–æ–∫ —Å –∫–∞—Ä—Ç–æ—á–∫–∞–º–∏ –ø–æ –º–∞—Ä—à—Ä—É—Ç–∞–º –ø—É—Ç–µ—à–µ—Å—Ç–≤–∏—è
const directionsList = directionsBlock.querySelector('#directions__list');

/* –ü—Ä–æ–≤–µ—Ä–∏–º, —Å–æ–æ—Ç–≤–µ—Ç—Å—Ç–≤—É–µ—Ç –ª–∏ —É–∫–∞–∑–∞–Ω–Ω—ã–π –º–µ–¥–∏–∞-–∑–∞–ø—Ä–æ—Å (screen and (max-width: 1023px)) —É—Å—Ç—Ä–æ–π—Å—Ç–≤—É
–†–µ–∑—É–ª—å—Ç–∞—Ç –ø—Ä–æ–≤–µ—Ä–∫–∏ –º–æ–∂–Ω–æ –ø–æ–ª—É—á–∏—Ç—å —Å –ø–æ–º–æ—â—å—é —Å–≤–æ–π—Å—Ç–≤–∞ matches (true –∏–ª–∏ false) */
if (window.matchMedia('screen and (max-width: 1023px)').matches) {
  directionsList.addEventListener('click', (evt) => {
    const card = evt.target.closest('.card');
    const cardBack = evt.target.closest('.card-back');
    if (evt.target.tagName === 'IMG') {
      card.classList.add('rotation');
    }
    if (cardBack) {
      card.classList.remove('rotation');
    }
  });
}
